/* global kakao */
import { useLayoutEffect } from "react";
import { IceMap, IceMapContainer } from "./styles";

/* Kakao Map */
declare var kakao: any;

interface Props {
  latitude: string;
  longitude: string;
}

// 
function KakaoMap(props: Props) {
  useLayoutEffect(() => {
    let [lat, lng] = [props.latitude, props.longitude];
    if (lat && lng) {
      // GPS   
      try {
        //  
        mapscript(lat, lng);
      } catch (e) {
        console.log(e);
      }
    }
  }, [props]);

  //   
  const mapscript = (lat: any, lng: any) => {
    let container = document.getElementById("IceLocaionMap");
    let options = {
      center: new kakao.maps.LatLng(lat, lng),
      level: 3,
    };
    //
    const map = new kakao.maps.Map(container, options);

    //   
    let markerPosition = new kakao.maps.LatLng(lat, lng);

    //  
    let marker = new kakao.maps.Marker({
      position: markerPosition,
    });

    //    
    marker.setMap(map);
  };

  return (
    <>
      <IceMapContainer>
        <IceMap id="IceLocaionMap" />
      </IceMapContainer>
    </>
  );
}

export default KakaoMap;
